import React, { Component } from 'react';
import injectSheet from 'react-jss';
import { styles } from './styles.js';


class About extends Component {
  render() {
    const { classes } = this.props;
    const resume = `tudiant en 5me anne  EPITECH et`
    + ` orient dans le dveloppement web et mobile, je travaille principalement`
    + ` avec JavaScript et NodeJS avec une affection particulire pour React/React Native.`
    + ` Je matrise l'environnement Linux et Windows ainsi que le logiciel de gestion de version Git.`;
    const available = `Rsidant  Paris, je suis actuellement  la recherche d'un stage en part-time en France pour septembre.`;
    return (
        <div className={classes.about}>
          <div className={classes.backgroundPP}><div className={classes.PP}></div>
          </div>
          <div className={classes.content}>
            <div className={classes.header}>
              <div className={classes.headerText}> PROPOS</div>
            </div>
            <div className={classes.resume}>
              <div className={classes.resumeText}>
                <span>
                  {resume}
                  <br/><br/>
                  {available}
                </span>
            </div>
            </div>
          </div>
        </div>
      )
    }
}

export default injectSheet(styles)(About)



