import React, { Component } from 'react';
import injectSheet from 'react-jss';
import { styles } from './styles.js';
import CardFormation from './CardFormation.js'

const detailsFormation = [
  {
    place: 'Lyce Xavier MARMIER, Pontarlier',
    title: 'Baccalaurat Scientifique',
    date: [new Date('2010-09-01'), new Date('2013-06-01')],
    description: ''
  },
  {
    place: 'Lyce Jules HAAG, Besanon',
    title: `BTS Informatique et Rseaux pour l'Industrie et les Services techniques`,
    date: [new Date('2013-09-01'), new Date('2015-06-01')],
    description: `Apprentissage du C/C++ ainsi que la programmation oriente objet, j'ai acquis durant ces 2 annes des comptences en administration systme et rseaux.`
  },
  {
    place: 'EPITECH, Strasbourg',
    title: 'Expert en Technologies de lInformation',
    date: [new Date('2015-10-01'), new Date('2019-06-01')],
    description: `Entre  EPITECH en deuxime anne, j'ai appris  travailler de faon autonome sur des langages comme le C/C++, Python et JavaScript. La troisime anne m'a form  la gestion de projets avec notamment un projet de fin d'tude d'une dure de 3 ans.`
  },
  {
    place: 'Jnkping University, Sude',
    title: 'change universitaire (4me anne EPITECH)',
    date: [new Date('2017-09-01'), new Date('2018-06-01')],
    description: `Dcouverte d'un nouveau pays, d'une nouvelle culture et  travailler en groupe dans un environnement universitaire.`
  }
]


class Formation extends Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.formation}>
        <div className={classes.header}>
          <div className={classes.headerText}>FORMATION</div>
        </div>
        <div className={classes.wrapperCards}>
          {
            detailsFormation.map((e, i) => <CardFormation key={`card-${i}`} details={e}/>)
          }
        </div>
      </div>
      )
    }
}

export default injectSheet(styles)(Formation)



